import { React } from 'react'
import { ReactComponent as Back } from '../assets/back.svg'
import '../css/Download.css'

const Download = ({ activeDownload, setActiveDownload }) => {
	const style = {
		display: activeDownload ? 'flex' : 'none',
	}
	const validate = true
	return (
		<div className='Page Download' style={style}>
			<div
				className='back'
				onClick={() => {
					console.log('d')
					setActiveDownload(false)
				}}
			>
				<Back />
				<p>Вернуться</p>
			</div>
			{validate ? (
				<main>
					<img className='header' src='' alt='' />
					<h1>Название</h1>
					<h2>Описание</h2>
					<img className='youtube' src='' alt='' />
					<div className='media-list'>
						<div className='card'>
							<img src='' alt='' />
							<p>Описание изображения</p>
						</div>
						<div className='card'>
							<img src='' alt='' />
							<p>Описание изображения</p>
						</div>
					</div>
					<details>
						<summary>Обновления</summary>
						<div className='updates-list'>
							<p>1.0: none</p>
							<p>1.1: none</p>
						</div>
					</details>
					<div className='downloads'>
						<div
							className='download'
							onClick={() => {
								setActiveDownload(false)
							}}
						>
							<p>Скачать</p>
							<h6>2.000 скачиваний</h6>
						</div>
					</div>
				</main>
			) : (
				<div className='invalidPage'>Похоже, данный мод был удалён</div>
			)}
		</div>
	)
}

export default Download
